<template>
        <div class="box" v-on:click="y" >            
            <div v-if="a">{{text}}</div> 
            <div v-else ></div>    
        </div>
</template>

<script>
    export default{
        props: ["n",'finished'],
        data(){
        return{a:false,}
        },
        methods:{
            y(){
                this.a=true;
                if(this.n%2==0){
                    this.text="X"
                }else{
                    this.text="O"
                }
                this.$emit("click",this.text);
            }
        }
    }
</script>

<style>
    .box{
        border: 1px solid black;
        height: 100px;
        width: 100px;
        display: flex;
        justify-content: center;
        font-size: 80px;
    
    }

</style>



