<template>
    <div>
    <h1>{{result}}</h1>
    <div class="chess" > 
    <div class="row">
        <chess v-on:click="hit(0,$event)" :n="n"/>
        <chess v-on:click="hit(1,$event)" v-bind:n="n"/>
        <chess v-on:click="hit(2,$event)" v-bind:n="n"/>
    </div>
    <div class="row">
        <chess v-on:click="hit(3,$event)" v-bind:n="n"/>
        <chess v-on:click="hit(4,$event)" v-bind:n="n"/>
        <chess v-on:click="hit(5,$event)" v-bind:n="n"/>
    </div>
    <div class="row">
        <chess v-on:click="hit(6,$event)" v-bind:n="n"/>
        <chess v-on:click="hit(7,$event)" v-bind:n="n"/>
        <chess v-on:click="hit(8,$event)" v-bind:n="n"/>
    </div>
</div>

</div>
</template>

<script>
import chess from './chess'
export default {
    components: { chess},
    data(){
       return {
           n:0,
           map:[[null,null,null],[null,null,null],[null,null,null]],
           result:"胜负未分",
           
       } 
    },
    methods:{
        hit(i,text){  
            console.log("第"+i+"个被点击，内容是"+text);
            if(this.n<=8){
                this.n=this.n+1;
            }else{
                return false;
            }
            this.map[Math.floor(i/3)][i%3]=text;
            console.log(this.map);
            const map=this.map;
            for(let k=0;k<3;k++){
                if(map[k][0]!=null&&map[k][0]==map[k][1]&&map[k][1]==map[k][2]){
                    this.result= "获胜的是"+map[k][0]
                 
                }else if(map[0][k]!=null&&map[0][k]==map[1][k]&&map[1][k]==map[2][k]){
                    this.result= "获胜的是"+this.map[0][k]
              
                }else if(map[1][1]!=null&&map[1][1]==map[0][0]&&map[0][0]==map[2][2]){
                    this.result= "获胜的是"+this.map[1][1]
               
                }else if(map[1][1]!=null&&map[0][2]==map[1][1]&&map[1][1]==map[2][0]){
                    this.result= "获胜的是"+this.map[1][1]
                   
                }
             
            }
                     
        }
    }

}
</script>

<style>
    .row{
        display: flex;
       
    }
    .chess{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
</style>



