<template>
<body>
  <div class="head">
    <img src="../assets/bg.jpg" class="img">
    <div class="nav">
      <ul>
        <li>首页</li>
        <li>编辑</li>
        <li>项目</li>
        <li>运行</li>
        <li>终端</li>
      </ul>
    </div>
  </div>
  <div class="main">
    <div class="left"></div>
    <div class="center"></div>
    <div class="right"></div>
  </div>
   
  <div class="bottem">
    <div class="imageLists">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">
      <img src="../assets/bg.jpg" class="img">  
    </div>
  </div>
</body>
</template>

<script>
  export default {
     data () {
       return {
       
       };
     }
   }
 </script>

  <style>
    *{
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }
    .head{
      background-color: grey;
      height: 100px;
      display: flex;
      flex-direction: row;
      color: honeydew;
    }
    .img{
      height: 80px;
      min-width: 160px;
      margin-top: 10px;
      vertical-align: middle;
    }
    .nav{
      border: 1px solid red;
      height: 100px;
      margin-left: 100px;
    }
    .nav ul{
      list-style: none;
      display: flex;
      flex-direction: row;
      margin-top: 30px;
    }
    li{
      margin-left: 100px;
    }
    .main{
      outline:1px solid green;
      height: 400px;
      width: 1000px;
      margin-top:20px;
      margin-left: auto;
      margin-right: auto;
      display: flex;
    }
    .main>.left{
      border:1px solid red;
      min-width: 200px;
      height: 400px;
      background-color: grey;
    }
    .main>.center{
      border:1px solid gray;
      min-width: 500px;
      height: 400px;
      background-color:red;
    }
    .main>.right{
      border:1px solid black;
      height: 400px;
      min-width: 300px;
      background-color: black;
    }
    .bottem{
      border:1px solid green;
      height: 210px;
      width: 1000px;
      margin-right: auto;
      margin-left: auto;
    }
    .imageLists>.img{
      margin-top: 10px;
      height: 200px;
      width: 190px;
      margin-right:80px;
    }
    .imageLists{
      margin-right: -160px;
      display: flex;
      flex-wrap: wrap;
      
    }
    
  </style>




